import createDecorator from "final-form-calculate";
import {FIELDS} from "./constants";


const valuesChecker = value => {
  if (value === undefined || value === null || isNaN(parseFloat(value))) {
    return 0
  } else {
    return Number(value)
  }
}

const multiplier = (a, b) => parseFloat(valuesChecker(a) * valuesChecker(b))

const divider = (a, b) => {
  const result = parseFloat(valuesChecker(a) / valuesChecker(b));
  return isFinite(result) && !isNaN(result) ? result : 0
}

const feeder = (a, b) => parseFloat(valuesChecker(a) + valuesChecker(b));

export const calculateShares = (existingShares, numberOfSharesBeforeIncrease) =>
  divider(existingShares, numberOfSharesBeforeIncrease)

export const calculateSubscriptionAmount = (addedShares, shareSubscriptionPrice) =>
  multiplier(addedShares, shareSubscriptionPrice)

const calcNewShares = (values = []) => {
  return values.length > 0 ? values
    .map(({addedShares}) => addedShares ? valuesChecker(addedShares) : 0)
    .reduce((acc, val) => feeder(acc, val)) : 0
}
const calcSubscribtionAmount = (values = []) => {
  return values.length > 0 ? values
    .map(({subscriptionAmount}) => subscriptionAmount ? valuesChecker(subscriptionAmount) : 0)
    .reduce((acc, val) => feeder(acc, val)) : 0
}


export const calculator = createDecorator(
  {
    field: FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE,
    updates: {
      totalExistingShareCapital: (ignoredValue, allValues = {}) => {
        return multiplier(allValues[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], allValues[FIELDS.DENOMINATION]).toFixed(2)
      },
      numberOfSharesAfterIncrease: (ignoredValue, allValues = {}) => {
        return feeder(allValues[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], allValues[FIELDS.NUMBER_OF_NEW_SHARES])
      }
    }
  },
  {
    field: FIELDS.DENOMINATION,
    updates: {
      totalExistingShareCapital: (ignoredValue, allValues = {}) => {
        return multiplier(allValues[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], allValues[FIELDS.DENOMINATION]).toFixed(2)
      },
      sumIncreasedShareCapital: (ignoredValue, allValues = {}) => {
        return multiplier(allValues[FIELDS.NUMBER_OF_NEW_SHARES], allValues[FIELDS.DENOMINATION])
      },
    }
  },
  {
    field: FIELDS.NUMBER_OF_NEW_SHARES,
    updates: {
      sumIncreasedShareCapital: (ignoredValue, allValues = {}) => {
        return multiplier(allValues[FIELDS.NUMBER_OF_NEW_SHARES], allValues[FIELDS.DENOMINATION]).toFixed(2)
      },
      numberOfSharesAfterIncrease: (ignoredValue, allValues = {}) => {
        return feeder(allValues[FIELDS.NUMBER_OF_SHARES_BEFORE_INCREASE], allValues[FIELDS.NUMBER_OF_NEW_SHARES])
      },
    }
  },
  {
    field: FIELDS.TOTAL_EXISTING_SHARE_CAPITAL,
    updates: {
      totalShareCapital: (ignoredValue, allValues = {}) => {
        return feeder(allValues[FIELDS.TOTAL_EXISTING_SHARE_CAPITAL], allValues[FIELDS.SUM_INCREASED_SHARE_CAPITAL]).toFixed(2)
      },
    }
  },
  {
    field: FIELDS.SUM_INCREASED_SHARE_CAPITAL,
    updates: {
      totalShareCapital: (ignoredValue, allValues = {}) => {
        return feeder(allValues[FIELDS.TOTAL_EXISTING_SHARE_CAPITAL], allValues[FIELDS.SUM_INCREASED_SHARE_CAPITAL]).toFixed(2)
      },
    }
  },
  {
    field: FIELDS.GENERAL_MEETING_TABLE_VALUES,
    updates: {
      numberOfNewShares: (ignoredValue, allValues = {}) => {
        return calcNewShares(allValues[FIELDS.GENERAL_MEETING_TABLE_VALUES]);
      },
      perMoneyValuation: (ignoredValue, allValues = {}) => {
        const {generalMeetingTableValues = [], perMoneyValuation} = allValues;
        let result = calcSubscribtionAmount(generalMeetingTableValues)
        result = parseFloat(result).toFixed(10) === parseFloat(perMoneyValuation).toFixed(10) ? result : perMoneyValuation
        return result
      },
    }
  },
  {
    field: FIELDS.PER_MONEY_VALUATION,
    updates: {
      shareSubscriptionPrice: (ignoredValue, currentValues, prevValues) => {

        const newShares = calcNewShares(currentValues[FIELDS.GENERAL_MEETING_TABLE_VALUES]);
        const perMoneyValuation = calcSubscribtionAmount(currentValues[FIELDS.GENERAL_MEETING_TABLE_VALUES])
        const result = prevValues[FIELDS.NUMBER_OF_NEW_SHARES] === currentValues[FIELDS.NUMBER_OF_NEW_SHARES] ?
          currentValues[FIELDS.PER_MONEY_VALUATION] : perMoneyValuation

        return parseFloat(divider(result, newShares)).toFixed(10) === parseFloat(currentValues[FIELDS.SHARE_SUBSCRIPTION_PRICE]).toFixed(10) ?
          currentValues[FIELDS.SHARE_SUBSCRIPTION_PRICE] :
          divider(result, newShares)
      },
    }
  },
  {
    field: FIELDS.SHARE_SUBSCRIPTION_PRICE,
    updates: {
      [FIELDS.PER_MONEY_VALUATION]: (ignoredValue, currentValues) => {
        const newShares = calcNewShares(currentValues[FIELDS.GENERAL_MEETING_TABLE_VALUES]);
        return multiplier(currentValues[FIELDS.SHARE_SUBSCRIPTION_PRICE], newShares);
      },
    }
  },
);